<template>
	<view class="page">
		<button class="upload" @tap="choose">上传图片</button>
		<view class="grid">
			<image v-for="(src,i) in images" :key="i" class="img" :src="src" mode="aspectFill" @tap="preview(i)"></image>
		</view>
	</view>
</template>

<script setup>
import { ref } from 'vue'

const images = ref(Array.from({ length: 9 }).map(() => '/static/logo.png'))

function choose() { uni.showToast({ title: '选择图片（示例）', icon: 'none' }) }
function preview(i) { uni.previewImage({ current: i, urls: images.value }) }
</script>

<style scoped>
.page { padding: 16rpx; }
.upload { margin-bottom: 12rpx; background: #ffd54f; color: #333; }
.grid { display: grid; grid-template-columns: repeat(3, 1fr); gap: 8rpx; }
.img { width: 100%; height: 220rpx; background: #f5f5f5; border-radius: 8rpx; }
</style>
